import { useState } from "react"
import Header from "./components/Header"
import UserInput from "./components/UserInput"
import Tasks from "./components/Tasks"

function App() {

  const [tasks, setTasks] = useState([]);


  const sortTasks = (tasks) => {
    return tasks.sort((a, b) => (a.important === b.important ? 0 : a.important ? -1 : 1)) // 0:equal -1:a first 1:b first
  }

  const addTask = (task) => {
    // setTasks([...tasks, task].sort((a, b) => (a.important === b.important ? 0 : a.important ? -1 : 1)))
    setTasks(sortTasks([...tasks, task]))
  }

  const deleteTask = (id) => {
    setTasks(tasks.filter(task => task.id !== id))
  }

  const toggleImportance = (id) => {
    setTasks(sortTasks(tasks.map(task => task.id === id ? { ...task, important: !task.important } : task)))
  }

  return (
    <div className="container">
      <Header title="Todo List" />
      <UserInput onSubmit={addTask} />
      <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleImportance} />
    </div>
  );
}

export default App;
