import { useState } from "react"


function UserInput({ onSubmit }) {

  const [text, setText] = useState('')
  const [date, setDate] = useState('')
  const [important, setImportant] = useState(false)

  const validate = (e) => {
    e.preventDefault()
    if (!text || !date) { //check if input field are empty
      alert('Please enter a task and a date')
      return
    }
    const id = Math.ceil(Math.random() * 10000)
    onSubmit({ id, text, date, important })
    setText('')
    setDate('')
    setImportant(false)
  }

  return (
    <form className="user-input" onSubmit={validate}>

      <div className="form-control">
        <label htmlFor="taskName">Task</label>
        <input type="text"
          id="taskName"
          value={text}
          placeholder="Enter Task..."
          onChange={(e) => setText(e.target.value)} />
      </div>

      <div className="form-control">
        <label htmlFor="taskDate">Date</label>
        <input type="text"
          id="taskDate"
          value={date}
          placeholder="Enter Date..."
          onChange={(e) => setDate(e.target.value)} />
      </div>

      <div className="form-control-checkbox">
        <input type="checkbox"
          id="checkbox"
          onChange={(e) => setImportant(e.target.checked)}
          checked={important} />
        <label htmlFor="checkbox">Mark as Important </label>
      </div>

      <button className="btn submit-btn" type="submit">ADD TASK</button>

      <hr />
    </form>
  )
}

export default UserInput
