import { FaTrash } from "react-icons/fa"
import { AiOutlineStar, AiFillStar } from "react-icons/ai";

function Task({ task, onDelete, onToggle }) {
  return (
    <div className="task">

      <div className="task-content">
        {/* start icon */}
        <div className="icon star-icon" onClick={() => onToggle(task.id)}>
          {
            task.important ?
              <AiFillStar style={{ fill: "#f5d742", cursor: "pointer" }} />
              :
              <AiOutlineStar style={{ fill: "#c7c7c7", stroke: "purple", cursor: "pointer" }} />
          }
        </div>

        {/* task info */}
        <div className="task-data">
          <h4 className="task-text">{task.text}</h4>
          <p className="task-day">{task.date}</p>
        </div>
      </div>

      {/* delete icon */}
      <div className="icon">
        <FaTrash style={{ fill: "crimson", cursor: "pointer" }}
          onClick={() => onDelete(task.id)} />
      </div>

    </div>
  )
}

export default Task
